/*  <general>  */
html {
  font-size: 62.5%;
  cursor: default;
}
-webkit-text-size-adjust 100%,
-ms-text-size-adjust 100% {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #3f4040;
  font: normal 1.6rem/1.5 'Rubik';
  background-color: #fff;
}
:-moz-placeholder {
  opacity: 1;
  color: currentColor;
}
:-ms-input-placeholder {
  opacity: 1;
  color: currentColor;
}
a {
  color: #0098dc;
  font: inherit;
  text-decoration: none;
  transition: all 0.2s;
}
a:hover {
  text-decoration: underline;
}
img {
  max-width: 100%;
}
.main {
  flex: 1;
}
/*  </general>  */
/*  <header>  */
.header {
  flex-shrink: 0;
  text-align: center;
}
.header__target {
  font-size: 1.8rem;
  text-transform: uppercase;
  border-bottom: 6px solid #dfdddb;
  padding: 15px 0 10px;
}
@media screen and (min-width: 480px) {
  .header__target {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}
.header__target span {
  display: block;
  margin-top: 10px;
}
@media screen and (min-width: 480px) {
  .header__target span {
    margin-top: 0;
  }
}
@media screen and (min-width: 480px) {
  .header__main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.header__logo {
  margin: 20px 0;
}
@media screen and (min-width: 480px) {
  .header__logo {
    width: 45%;
    text-align: left;
    margin-bottom: 0;
  }
}
.header__logo img {
  max-width: 222px;
}
.header__logo-slogan {
  font-size: 1.8rem;
  margin-top: 5px;
}
.header__contacts {
  font-size: 1.8rem;
  border-bottom: 6px solid #dfdddb;
  padding-bottom: 15px;
}
@media screen and (min-width: 480px) {
  .header__contacts {
    width: 45%;
    border-bottom: 0;
    margin-top: 20px;
  }
}
.header__contacts-phone {
  color: #3f4040;
  font-weight: bold;
  font-size: 2rem;
  text-decoration: none;
}
.header__contacts-workhours {
  font-weight: 300;
}
.header__contacts-links {
  font-weight: 500;
  margin-top: 5px;
}
.header__contacts-links a {
  color: #38ae25;
  text-decoration: underline;
}
.header__contacts-links a:last-child {
  display: none;
}
.header__contacts-links a:hover {
  text-decoration: none;
}
.header__action {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0 10px;
}
@media screen and (min-width: 480px) {
  .header__action {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    text-align: left;
    border-top: 6px solid #dfdddb;
    padding-top: 20px;
    margin-top: 0;
  }
}
.header__action-item {
  position: relative;
  color: #3f4040;
  font-size: 1.8rem;
  padding-top: 44px;
  padding-bottom: 5px;
  margin-bottom: 20px;
}
@media screen and (min-width: 480px) {
  .header__action-item {
    width: 30%;
  }
}
.header__action-item::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #ed8223;
  content: '';
  transition: all 0.2s;
}
a:hover.header__action-item {
  text-decoration: none;
}
a:hover.header__action-item::after {
  width: 100%;
}
.header__action-item svg {
  position: absolute;
  top: 0;
  left: 50%;
  width: 35px;
  height: 35px;
  fill: #3f4040;
  transform: translateX(-50%);
  transition: all 0.2s;
}
@media screen and (min-width: 480px) {
  .header__action-item svg {
    left: 0;
    transform: none;
  }
}
a:hover.header__action-item {
  color: #ed8223;
}
a:hover.header__action-item svg {
  fill: #ed8223;
}
.header__action-item:last-child {
  margin-bottom: 0;
}
/*  </header>  */
/*  <nav>  */
.nav {
  flex-shrink: 0;
  background-color: #38ae25;
}
.nav__main {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 15px;
}
@media screen and (min-width: 480px) {
  .nav__main {
    margin-left: -28px;
  }
}
.nav__item {
  width: 55%;
  color: #fff;
  font-size: 1.8rem;
  text-transform: uppercase;
  margin-top: 20px;
}
@media screen and (min-width: 480px) {
  .nav__item {
    width: auto;
    margin-top: 15px;
    margin-left: 28px;
  }
}
.nav__item:nth-child(even) {
  width: 45%;
}
@media screen and (min-width: 480px) {
  .nav__item:nth-child(even) {
    width: auto;
  }
}
.nav__item--arrow {
  display: inline-flex;
  align-items: center;
}
.nav__item--arrow::after {
  width: 13px;
  height: 13px;
  border: solid #fff;
  border-width: 0 0 2px 2px;
  margin-left: 10px;
  transform: translateY(calc(-50% + 1px)) rotate(-45deg);
  content: '';
}
/*  </nav>  */
/*  <footer>  */
.footer {
